.extra-block{
	padding: 30px 0;
	border-bottom: 1px solid #D8DFE2;
	&__zag{
		width: 100%;
		text-align: center;
		font-size: 36px;
		line-height: 1.3;
		color: #232323;
		margin-bottom: 45px;
	}
	&__container{
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: flex-start;
		width: calc(100% + 50px);
	}
	.catalog-item{
		width: calc(100% - 50px);
	}
}